@import url('https://fonts.googleapis.com/css2?family=Mansalva&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@500;600&display=swap');

:root {
    --deep-green: #605828;
    --orange: #f9a115;
    --blush: #fcc5ac;
    --charcoal: #1e1d1d;
    --blue: #a9ccff;
}
html {
    background-color: #e7d8c0;
}
html, body {
    height: 100%;
}

body {
    display: flex;
    flex-flow: column nowrap;
    background-color: none;
    max-width: 100%;
    font-family: 'Mansalva', cursive;
    justify-content: center;
    align-items: center;
    color: var(--orange);
    
    
}

h1 {
    position: fixed;
    top: 1rem;
    font-family: 'Cormorant Garamond', serif;
    color: var(--deep-green);
    font-weight: bold;
}

.flex-todos {
    position: fixed;
    top: 10.3rem;
}

.the-list {
    line-height: 3rem;
    position: relative;
    align-items: flex-start;
    color: var(--deep-green);
    font-size: 1.2rem;
    top: -2.5rem;
}


.buttons {
    display: flex;
    position: fixed;
    z-index: 2;
    flex-flow: column;
    position: fixed;
    margin-top: 38rem;
    
}



.input-and-add {
    display: flex;
    align-items: baseline;
}

.todo-input {
    font-family: 'Mansalva', cursive;
    background-color: #fcc5ac;
    font-size: 1.2rem;
    border: none;
    width: 10rem;
    height: 3rem;
    color: white;
    border-radius: 2rem;
    margin-right: 1rem;
    transition: background-color 1s linear;
}

.todo-input:hover {
    background-color: var(--deep-green);
    color: var(--blush);
    border: none;
    height: 3rem;
    
}

input[type=checkbox] {
    width: 1.5rem;
    accent-color: var(--blush);
  }

.add {
    
    vertical-align: baseline;
    border-radius: 50%;
    padding: 1rem .7rem;
    aspect-ratio: 1;
    border: none;
    background-color: #a9ccff;
    color: #405d89;
    font-family: 'Mansalva', cursive;
    cursor: pointer;

}

.add:hover {
    border-radius: 50%;
    aspect-ratio: 1;
    
    background-color: #f9a115;
    color:#e7d8c0;
}

.left-and-clear {
    display: flex;
}

.left {
    
    border-radius: 50%;
    aspect-ratio: 1;
    text-align: center;
    background-color: var(--orange);
    color: var(--deep-green);
    font-weight: 700;
    max-width: 4rem;
    padding: .5rem;
    padding-bottom: .1rem;
    transition: background-color .5s ease-in;
}

.left:hover {
    background-color: var(--blue);
    color: var(--deep-green);
}

.clear {
    display: block;
    font-family: 'Cormorant Garamond', serif;
    border: none;
    padding-right: 1rem;
    padding-left: 1rem;
    margin: 1rem;
    cursor: pointer;
    background-color: var(--deep-green);
    color: var(--blush);
    border-radius: 2rem;
    max-height: 4rem;
    transition: background-color .1s ease-in;
    
}

.clear:hover {
    background-color: var(--blush);
    color: var(--deep-green);
}


.paper {
    position: fixed;
    width: 40rem;
    top: -4rem;
    z-index: -1;
}


.illustrations {
    position: absolute;
    width: 70%;
    height: 100%;
    margin: 2rem 2rem;
    
    
}
.snail {
    position: absolute;
    top: -0.5rem;
    left: 16.5rem;
    width: 3.4rem;
    height: 3.8rem;
    
}

.snail-trail {
    position: absolute;
    top: 2.4rem;
    left: 15.5rem;
    width: 3rem;
    height: 1rem;
    z-index: -1;
}

.leaf {
    position: absolute;
    top: 1rem;
    right: 7rem;
    max-width: 1rem;
    max-height: 2rem;
    transform: rotate(20deg);
}

.flower {
    position: absolute;
    top: 15rem;
    left: 6rem;
    transform: scaleX(-1);
    width: 4.5rem;
}

.tiny-flower {
    position: absolute;
    right: 43rem;
    top: 45rem;
    width: .8rem;
    height: 2rem;
    

}

.tiny-flowers {
    position: absolute;
    top: 40rem;
    right: 50rem;
    width: 1rem;
    height: 1rem;
    transform: rotate(5deg);
}

.sunflower {
    position: absolute;
    right: 10.6rem;
    bottom: 19rem;
    width: 1.5rem;
    height: 1.5rem;   
}

.big-leaf {
    position: absolute;
    top: 35.5rem;
    right: 15.2rem;
    width: 4rem;
    height: 6rem;  
}

.tiny-flower2 {
    position: absolute;
    left: 2rem;
    top: 4rem;
    width: .7rem;
    transform: rotate(37deg);
}

.tiny-flowers2 {
    position: absolute;
    right: 12rem;
    top: 14rem;
    width: 2rem;
    transform: rotate(-57deg);
}

